.logo{display:block;text-indent:-9999px;width:100px;height:82px;background:url("/images/fw-logo.svg");background-size:100px 82px}.header{min-height:75px}.header-nav{display:flex;align-items:center;padding:40px 60px;position:fixed;right:0;left:0;top:0;height:75px;z-index:9;}.header-nav__logo{display:block;flex-shrink:0;background:#162331;border-radius:50%;transition:background ease .3s;height:70px;width:70px;}.header-nav__logo:hover{background:#23364b}.header-nav__logo .logo{margin:auto;height:70px;width:25px;background-size:25px 70px}.header-nav__brand-name{display:none;position:absolute;left:0;right:0;text-align:center;font-size:31px;font-weight:300}.header-nav__bar{display:flex;}.header-nav__bar__link{display:flex;padding:12px 30px;letter-spacing:.1rem;text-transform:uppercase;font-weight:600;font-size:12px;font-family:'Open Sans',sans-serif;color:#141414;}.header-nav__bar__link:hover{background:#fff;border-radius:100px}.header-nav__bar__link:nth-child(2):hover{background:#03a9f4;color:#fff}.header-nav__bar__link:nth-child(3):hover{background:#00d5b5;color:#fff}.header-nav__bar__link:nth-child(4):hover{background:#f33155;color:#fff}.header-nav__bar__link:first-child{margin-left:30px}.header-nav__bar__link > i{margin-right:25px;font-size:24px}